<style lang="less" scoped>
  .el-date-editor--datetimerange.el-input__inner {
    width: 400px;
  }
  .el-date-editor--daterange.el-input__inner {
    width: 224px;
  }
  .el-input {
    width: 200px;
  }
</style>
<template>
  <div>
    <div class="page-container">
      <div class="table-filters">
        <div class="filter-item">
          <p class="item">时间</p>
          <el-date-picker
            v-model="checkDate"
            size="small"
            type="datetimerange"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </div>
        <div class="filter-item">
          <p class="item">工号</p>
          <el-input v-model="filterCondition.employee_code"
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class="filter-item">
          <p class="item">姓名</p>
          <el-input v-model="filterCondition.user_name"
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class="filter-item">
          <p class="item">角色名称</p>
          <el-input v-model="filterCondition.role_name"
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class='search-btn'>
          <el-button type="primary"
                     size="small"
                     icon="el-icon-search"
                     @click="requestStatisticsList">查询
          </el-button>
        </div>
      </div>
      <el-table
        :data="tableData"
        size="mini"
        style="width: 100%">
        <el-table-column
          fixed
          prop="id"
          type="index"
          label="序号"
          width="60">
        </el-table-column>
        <el-table-column
          prop="user_name"
          label="姓名">
        </el-table-column>
        <el-table-column
          prop="employee_code"
          label="工号">
        </el-table-column>
        <el-table-column
          prop="department_name"
          label="部门">
        </el-table-column>
        <el-table-column
          prop="op_type"
          label="操作类型">
          <template slot-scope="scope">
            <span :style="{color: '#FFAA00'}">
              {{$utils.checkOPType(scope.row.op_type)}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="role_name"
          label="角色名称">
        </el-table-column>
        <el-table-column
          prop="role_department_name"
          label="角色所属部门">
        </el-table-column>
        <el-table-column
          prop="created_at"
          label="时间"
          width="160">
          <template slot-scope="scope">
            {{scope.row.created_at | datetime}}
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        v-show="page.total > 0"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="page.sizes"
        layout="sizes,prev,pager,next"
        :current-page.sync="page.current"
        :total="page.total"
        prev-text="上一页"
        next-text='下一页'>
      </el-pagination>
    </div>
  </div>
</template>

<script>
import api from '../../api'

export default {
  components: {
  },
  data () {
    return {
      page: {
        total: 0,
        size: 10,
        current: 1,
        sizes: [10, 50, 100, 200]
      },
      checkDate: '',
      // 查询条件
      filterCondition: {
        user_name: '',
        // 工号
        employee_code: '',
        role_name: ''
      },
      tableData: []
    }
  },
  methods: {
    // 获取权限管理列表数据
    requestStatisticsList () {
      if (this.checkDate == null) {
        this.checkDate = ''
      }
      let params = {
        ...this.filterCondition,
        start_time: this.checkDate[0],
        end_time: this.checkDate[1],
        page: this.page.current,
        per_page: this.page.size
      }
      api.getStatisticsPermission(params).then(res => {
        console.log(res)
        if (res.code === 200) {
          let result = res.result || {}
          this.page.total = result.pagination ? result.pagination.total_count : 0
          this.tableData = result.data || []
        } else {
          this.$message.error(res.message)
        }
      })
    },
    // 获取每页条数
    handleSizeChange (size) {
      this.page.size = size
      this.requestStatisticsList()
    },
    // 获取当前页
    handleCurrentChange (index) {
      this.page.current = index
      this.requestStatisticsList()
    }
  },
  beforeMount () {
    this.requestStatisticsList()
  }
}
</script>



